* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #181818;
  color: #fff;

}

.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  align-items: center;
  gap: 5em;
  box-sizing: border-box;
}

.App>h1 {
  font-size: 4em;
  margin: 50px;
}

.App>.TypingSection {
  border: 1px solid aqua;
  padding: 10px;
  border-radius: 20px;
  width: 60vw;
  font-size: 1.2em;

}

.App>.TypingSection>.inputArea {
  width: 100%;
  display: grid;
  grid-template-columns: 5fr 1fr 1fr;
  gap: 1em;
}

.App>.TypingSection>.inputArea>input {
  padding: 5px 10px;
  font-size: 1.2em;
  width: 90%;
  outline: none;
  border-radius: 10px;
  border: none;
}

.App>.TypingSection>.inputArea>.timer {
  width: 100%;
  background-color: gray;
  border-radius: 10px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App>.TypingSection>.inputArea>button {
  background-color: rgb(0, 98, 255);
  border: none;
  border-radius: 10px;
}

.wordsDisplay {
  display: flex;
  width: 100%;
  flex-flow: row wrap;
  font-size: 2em;
  height: 4em;
  line-height: 1.5em;
  overflow: hidden;

}

.wordsDisplay>span {
  padding: 5px 10px;
  border-radius: 10px;
  /* font-size: 2em; */
  margin: 5px;
}

.pendingWord {
  background-color: #333 !important;

}

.wrongWord {
  background-color: red !important;
  color: #000 !important;
}

.correctWord {
  color: rgb(30, 255, 0) !important;
  /* color: #000; */
}

.result {
  border: 1px solid aqua;
  border-radius: 10px;
  padding: 10px;
  font-size: 2em;
}

.result .bestSpeed,
.result .wpmSpeed,
.result .correctWordCount {
  color: lime;

}


.result .wrongWordCount {
  color: red;
}